* {
  font-family: Arial, sans-serif;
  font-size: 14px;
}

.clear-all-button {
  border: none;
  background: transparent;
  color: #444;
  cursor: pointer;
  font-size: 20px;
  padding: 0 8px;
  display: flex;
  align-items: center;
  height: 32px;
  font-weight: bold;
  margin-left: 4px;
}

/* iOS specific media query */
@supports (-webkit-touch-callout: none) {
  .clear-all-button {
    font-size: 28px;
    font-weight: 900;
    padding: 0 12px;
  }
}